    margin: 1em .5em;
}

.newItem label{
    width: 10em;
    text-align: left;
}
.newItem-name, .newItem-quantity{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.newItem-name input{
    width: 30em;
}
.newItem-quantity{
    width: 4em;
}
.newItem-quantity input{
    width: 5em;
    text-align: right;
}

.newItem-actions{
    width: 100%;
    text-align: right;
}

.newItem button{
    width: 5em;
}




